//Euclidean 3D Distance Function
const calculate_distance = (v1, v2) => {
    return Math.sqrt(
        (v1.x - v2.x) * (v1.x - v2.x) +
        (v1.y - v2.y) * (v1.y - v2.y) +
        (v1.z - v2.z) * (v1.z - v2.z)
    )
}

//Function To Conver Wavelength NM To RGB Values
const nm_to_rgb = () => {

    //Code implemented from http://www.fourmilab.ch/documents/specrend/

    const wavelength = parameters.wavelength

    const gamma = 0.9,
        intensity_max = 255
    let red = 0,
        blue = 0,
        green = 0,
        factor = 0

    //Massive 4D Piecewise function
    if ((wavelength >= 380) && (wavelength < 440)) {
        red = -(wavelength - 440) / (440 - 380);
        green = 0.0;
        blue = 1.0;
    } else if ((wavelength >= 440) && (wavelength < 490)) {
        red = 0.0;
        green = (wavelength - 440) / (490 - 440);
        blue = 1.0;
    } else if ((wavelength >= 490) && (wavelength < 510)) {
        red = 0.0;
        green = 1.0;
        blue = -(wavelength - 510) / (510 - 490);
    } else if ((wavelength >= 510) && (wavelength < 580)) {
        red = (wavelength - 510) / (580 - 510);
        green = 1.0;
        blue = 0.0;
    } else if ((wavelength >= 580) && (wavelength < 645)) {
        red = 1.0;
        green = -(wavelength - 645) / (645 - 580);
        blue = 0.0;
    } else if ((wavelength >= 645) && (wavelength < 781)) {
        red = 1.0;
        green = 0.0;
        blue = 0.0;
    } else {
        red = 0.0;
        green = 0.0;
        blue = 0.0;
    }

    // Let the intensity fall off near the vision limits
    if ((wavelength >= 380) && (wavelength < 420)) {
        factor = 0.3 + 0.7 * (wavelength - 380) / (420 - 380);
    } else if ((wavelength >= 420) && (wavelength < 701)) {
        factor = 1.0;
    } else if ((wavelength >= 701) && (wavelength < 781)) {
        factor = 0.3 + 0.7 * (780 - wavelength) / (780 - 700);
    } else {
        factor = 0.0;
    }

    if (red !== 0) {
        red = Math.round(intensity_max * Math.pow(red * factor, gamma));
    }
    if (green !== 0) {
        green = Math.round(intensity_max * Math.pow(green * factor, gamma));
    }
    if (blue !== 0) {
        blue = Math.round(intensity_max * Math.pow(blue * factor, gamma));
    }

    return new THREE.Color(
        red,
        green,
        blue
    )
}

