//Laser Haze

//Defining the points globally
let laser_haze_points = null

const particle_system_laser_haze = (number_of_points, particle_size, particle_color) => {

    console.log("Particles Loaded")

    //Calculate the board position center relative to slit
    const board_mesh_position_relative_to_slit = new THREE.Vector3(
        board_mesh.position.x,
        single_slit_mesh.position.y,
        board_mesh.position.z
    )

    const distance_between_board_and_slit = calculate_distance(
        board_mesh_position_relative_to_slit,
        single_slit_mesh.position
    )

    const angle_spread_from_slit = Math.PI / 3

    //Particle Geometry
    const particle_geometry = new THREE.BufferGeometry()
    const particle_positions = new Float32Array(number_of_points * 3)

    const laser_haze_translate = 1.1
    const randomness = 0.25

    for (let i = 0; i < number_of_points; i++) {

        const i3 = i * 3

        const random_x = (Math.random() - 0.5) * randomness
        const random_y = (Math.random() - 0.5) * randomness
        const random_z = (Math.random() - 0.5) * randomness

        const x_ = (Math.random() * distance_between_board_and_slit) + laser_haze_translate
            //X
        particle_positions[i3 + 0] = -x_ + random_x
            //Y
        particle_positions[i3 + 1] = single_slit_mesh.position.y + random_y
            //Z
        particle_positions[i3 + 2] = (Math.tan(angle_spread_from_slit) * ((x_ % distance_between_board_and_slit) * (Math.sin((x_ % 0.5) * Math.PI) - 0.5))) + 0.1 + random_z
    }
    particle_geometry.setAttribute('position', new THREE.BufferAttribute(particle_positions, 3))

    //Materials
    const particle_material = new THREE.PointsMaterial()
    particle_material.size = particle_size
    particle_material.sizeAttenuation = true
    particle_material.depthWrite = false
    particle_material.blending = THREE.AdditiveBlending
    particle_material.color = particle_color

    laser_haze_points = new THREE.Points(particle_geometry, particle_material)
    scene.add(laser_haze_points)
}


//Laser Light

//Defining The Laser Light Globally
let laser_light_line = null

const laser_light = (particle_color) => {
    /**
     * Laser Light To Slit
     */
    console.log("Laser Light To Slit Loaded")
    const laser_light_material = new THREE.LineBasicMaterial({
        linewidth: 100
    })
    laser_light_material.color = particle_color
    const laser_light_points = []

    //Starting Point
    laser_light_points.push(new THREE.Vector3(laser_pointer_mesh.position))

    //Points In Between
    const distance_between_slit_and_laser = calculate_distance(
        laser_pointer_mesh.position,
        single_slit_mesh.position
    )

    const laser_light_line_translate = 0.1

    for (let i = 0; i < distance_between_slit_and_laser; i++) {
        laser_light_points.push(new THREE.Vector3(-(laser_light_line_translate + laser_pointer_mesh.position.x + i),
            laser_pointer_mesh.position.y,
            laser_pointer_mesh.position.z))
    }

    //Final Point
    laser_light_points.push(new THREE.Vector3(single_slit_mesh.position))

    //Create Line Geometry
    const laser_light_geometry = new THREE.BufferGeometry().setFromPoints(laser_light_points)

    //Create Line
    laser_light_line = new THREE.Line(laser_light_geometry, laser_light_material)
    scene.add(laser_light_line)
}

