//Defining the path length difference globally
let path_length_difference_mesh = null

//Defining the Wavelength Symbol Globally
let lambda_mesh_group = null

const create_path_length_difference = (font) => {

    //Calculate the board position center relative to slit
    const board_mesh_position_relative_to_slit = new THREE.Vector3(
        board_mesh.position.x,
        single_slit_mesh.position.y,
        board_mesh.position.z
    )

    const distance_between_inspection_point_and_slit = calculate_distance(
        inspection_point_sphere_mesh.position,
        single_slit_mesh.position
    )

    //Calculate the board position center relative to inspection point
    const board_mesh_position_relative_to_inspection_point = new THREE.Vector3(
        board_mesh.position.x,
        inspection_point_sphere_mesh.position.y,
        board_mesh.position.z
    )

    const distance_between_inspection_point_and_board_center = calculate_distance(
        inspection_point_sphere_mesh.position,
        board_mesh_position_relative_to_inspection_point
    )

    const distance_between_board_and_slit = calculate_distance(
        board_mesh_position_relative_to_slit,
        single_slit_mesh.position
    )

    //sine(0)/distance_between_vectors = sin(90)/vector_towards_inspection_point
    //sin(0) = (sin(90)/vector_towards_inspection_point)*distance_between_vectors
    //sin(0) = angle_between_vectors

    const extra_distance_in_wavelengths = ((parameters.slit_spacing * Math.pow(10, -6)) * (distance_between_inspection_point_and_board_center / distance_between_board_and_slit)) / (parameters.wavelength * Math.pow(10, -8)) * 2

    simulation_information.path_length_difference_simulation_info = extra_distance_in_wavelengths

    const textGeometry = new THREE.TextGeometry(
        ((extra_distance_in_wavelengths).toFixed(2)).toString(), {
            font: font,
            size: 0.15,
            height: 0.005,
            curveSegments: 6,
            bevelEnabled: true,
            bevelThickness: 0.00025,
            bevelSize: 0.0025,
            bevelOffset: 0,
            bevelSegments: 4
        }
    )
    textGeometry.center()

    const textMaterial = new THREE.MeshLambertMaterial()
    path_length_difference_mesh = new THREE.Mesh(textGeometry, textMaterial)

    //Positioning The Information
    path_length_difference_mesh.position.x = ((single_slit_mesh.position.x - calculate_distance(board_mesh_position_relative_to_slit, single_slit_mesh.position) + 0.75) / 2)
    path_length_difference_mesh.position.y = single_slit_mesh.position.y + 0.1
    path_length_difference_mesh.position.z = single_slit_mesh.position.z + 0.75

    //Rotating The Information 
    path_length_difference_mesh.rotation.x = -Math.PI / 2
    path_length_difference_mesh.rotation.z = Math.PI / 2

    scene.add(path_length_difference_mesh)

    //Adding Custom Wavelength Symbol

    //We can start by making two different rectangles
    const lambda_long = new THREE.BoxGeometry(0.05, 0.01, 0.4)
    const lambda_short = new THREE.BoxGeometry(0.05, 0.01, 0.25)

    const lambda_mat = new THREE.MeshLambertMaterial()

    lambda_mesh_group = new THREE.Group()

    const lambda_long_mesh = new THREE.Mesh(lambda_long, lambda_mat)

    //Positioning The Mesh
    lambda_long_mesh.position.x = 0.05
    lambda_long_mesh.position.z = -0.05

    //Rotating The Long Side
    lambda_long_mesh.rotation.y = Math.PI / 6

    const lambda_short_mesh = new THREE.Mesh(lambda_short, lambda_mat)

    lambda_mesh_group.add(lambda_long_mesh, lambda_short_mesh)

    //Positioning The Lambda Symbol
    lambda_mesh_group.position.x = ((single_slit_mesh.position.x - calculate_distance(board_mesh_position_relative_to_slit, single_slit_mesh.position) + 0.75) / 2)
    lambda_mesh_group.position.y = single_slit_mesh.position.y + 0.1
    lambda_mesh_group.position.z = single_slit_mesh.position.z + 0.4

    //Rotating The Symbol
    lambda_mesh_group.rotation.y = Math.PI / 2

    //Scaling The Symbol
    lambda_mesh_group.scale.set(0.7, 0.7, 0.7)

    scene.add(lambda_mesh_group)

}

