///Creating the angle mesh globally
let angleMesh = null

const create_light_ray_to_inspection_point_angle = (font) => {
    console.log("Angle Info Added To Scene")

    const distance_between_inspection_point_and_slit = calculate_distance(
        inspection_point_sphere_mesh.position,
        single_slit_mesh.position
    )

    //Calculate the board position center relative to inspection point
    const board_mesh_position_relative_to_inspection_point = new THREE.Vector3(
        board_mesh.position.x,
        inspection_point_sphere_mesh.position.y,
        board_mesh.position.z
    )

    const distance_between_inspection_point_and_board_center = calculate_distance(
        inspection_point_sphere_mesh.position,
        board_mesh_position_relative_to_inspection_point
    )

    //sine(0)/distance_between_vectors = sin(90)/vector_towards_inspection_point
    //sin(0) = (sin(90)/vector_towards_inspection_point)*distance_between_vectors
    //sin(0) = angle_between_vectors

    const angle_between_vectors = Math.asin((Math.sin(Math.PI / 2) / distance_between_inspection_point_and_slit) * distance_between_inspection_point_and_board_center)

    const angle_in_degree = ((angle_between_vectors * 180) / (2 * Math.PI)).toFixed(2)

    simulation_information.inspection_point_angle_simulation_info_rads = angle_between_vectors
    simulation_information.inspection_point_angle_simulation_info_deg = angle_in_degree

    const textGeometry = new THREE.TextGeometry(
        (angle_in_degree).toString() + " ", {
            font: font,
            size: 0.15,
            height: 0.005,
            curveSegments: 6,
            bevelEnabled: true,
            bevelThickness: 0.00025,
            bevelSize: 0.0025,
            bevelOffset: 0,
            bevelSegments: 4
        }
    )
    textGeometry.center()

    const textMaterial = new THREE.MeshLambertMaterial()
    angleMesh = new THREE.Mesh(textGeometry, textMaterial)

    //Positioning The Information
    angleMesh.position.x = single_slit_mesh.position.x
    angleMesh.position.y = single_slit_mesh.position.y + 0.1
    angleMesh.position.z = single_slit_mesh.position.z + 0.75

    //Rotating The Information 
    angleMesh.rotation.x = -Math.PI / 2
    angleMesh.rotation.z = Math.PI / 2

    scene.add(angleMesh)

}

