/**
 * Animate
 */

//Making A Intensity Graph
const interference_intensity_graph_animation = (x) => {
    //Calculate the board position center relative to slit

    const board_mesh_position_relative_to_slit = new THREE.Vector3(
        board_mesh.position.x,
        single_slit_mesh.position.y,
        board_mesh.position.z
    )

    const slit_board_distance = calculate_distance(board_mesh_position_relative_to_slit, single_slit_mesh.position)
    const wavelength = parameters.wavelength * Math.pow(10, -9)
    const slit_spacing = parameters.slit_spacing * Math.pow(10, -6)
    const light_intensity = parameters.light_intensity

    const x_value = (x / 30) % 1

    const p = ((slit_spacing * ((x_value) - (0.5))) / (slit_board_distance * wavelength)) * 2.0 * 3.14
    const i = (light_intensity * ((Math.sin(p / 2.0)) / (p / 2.0))) * (light_intensity * ((Math.sin(p / 2.0)) / (p / 2.0))) / 100.0;

    simulation_information.intensity_graph_simulation_info = i
}

const clock = new THREE.Clock()

const tick = () => {
    const elapsedTime = clock.getElapsedTime()

    //Call Function To Update The Intensity Value
    if (board_mesh != null) {
        interference_intensity_graph_animation(elapsedTime)
    }

    // Update controls
    controls.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()

